<template>
    <div class="main-quiz--section">
        <!-- HEADER AREA -->
        <questionsHeader 
            :name="quiz.name" />


        <!-- NO QUESTIONS AREA-->
        <questionsNotFound 
            v-if="shuffledData.length === 0"/>


        <!-- QUESTIONS AREA -->
        <div v-if="questionIndex < shuffledData.length">
            <div 
                v-for="(question, index) in shuffledData" :key="index"
                class="main-quiz-question-item--area"
            >
                <div v-show="index === questionIndex">
                    <span class="main-quiz-question-page--elem">{{ index+1 }} / {{ shuffledData.length }}</span>
                    <h2 class="main-quiz-question-title--elem">{{ question.title }}</h2>
                    <ol class="__row_style">
                        <li 
                            v-for="(response, i) in question.responses" :key="i"
                            class="main-quiz-question-response--elem response--area"
                            @click="updateUserResponces($event, index, response.correct)"
                            :data-index="`${i+1}.`"
                        >
                            {{response.text}}
                        </li>
                    </ol>
                    <div class="main-timer-countdown--elem" :class="{ isOff: countdownBlocker }">
                        <div class="main-timer-contdown-text--elem">
                            <p>Time Out: <span>{{ timeOut }}</span>.</p>
                        </div>
                        <div class="main-timer-contdown-bar--elem">
                            <!-- :style="{ width: `calc( ${barWidth}% - 7.5px)` }" -->
                            <div class="main-bar--elem" :style="{ width: `${barWidth}%` }"></div>
                        </div>
                    </div>
                    <a  class="main-quiz-question-global-btn--elem" @click="next()" v-show="countdown !== 0 || countdownBlocker === true">next<i class="las la-arrow-right"></i></a>
                    <a  class="main-quiz-question-global-btn--elem __block" v-show="countdown == 0 && countdownBlocker === false">next<i class="las la-arrow-right"></i></a>
                </div>
            </div>
        </div>
        

        <!-- SCORE AREA -->
        <questionsScore
            v-if="toggleScoreAreas && shuffledData.length > 0" 
            v-show="questionIndex === shuffledData.length && showScore" 

            :score="score"
            :total="shuffledData.length"
            :tags="tags"
            
            :onStartAgain="this.startAgain"
            :onToggleScoresAreasFn="this.toggleScoresAreasFn"/>


        <!-- CORRECT ANSWERS AREA -->
        <questionsAnswers 
            v-if="!toggleScoreAreas && shuffledData.length > 0" 
            v-show="questionIndex === shuffledData.length && showScore" 

            :answers="answerTable"
            
            :onStartAgain="this.startAgain"
            :onToggleScoresAreasFn="this.toggleScoresAreasFn"/>


        <!-- COUNTDOWN CONTROLLER AREA v-show="questionIndex < shuffledData.length" countdownBlocker ableCountDown() -->
        <questionsCountdownRemote 
            v-show="questionIndex < shuffledData.length"
            :block="countdownBlocker" 
            :onAbleCountDown="this.ableCountDown" />
    </div>
</template>

<script>
/* eslint-disable */
import moment from 'moment';

import questionsHeader from './questionsComponent/questionsHeader';
import questionsNotFound from './questionsComponent/questionsNotFound';
import questionsCountdownRemote from './questionsComponent/questionsCountdownRemote';
import questionsScore from './questionsComponent/questionsScore';
import questionsAnswers from './questionsComponent/questionsAnswers';

export default {
    props: ['quiz'],
    data() {
        return {
            shuffledData: [],
            // Store current question index
            questionIndex: 0,
            userResponses: [],
            showScore: false,
            score: 0,
            answerTable: [],
            toggleScoreAreas: true,

            // Cowntdown Varibales
            defaultNumber: 60,
            countdown: null,
            countdownTimer: null,
            countdownBlocker: false,
            barWidth: 0,

            // Answers Varibales
            tags: {
                icon: "005-ribbon-3.svg",
                title: "Nice!",
            },
        }
    },
    created() {
        this.getShuffledArray(this.quiz);
    },
    computed: {
        timeOut: function(){
            return this.countdownTimer.format('mm:ss');
        }
    },
    methods: {
        next() {  
            this.setCountDownData();
            this.getUserAnswersFn();
            this.questionIndex++;

            this.getUserDegreeData();
        },
        prev() { this.questionIndex--; },
        toggleScoresAreasFn(){
            this.toggleScoreAreas = !this.toggleScoreAreas;
        },
        startAgain() {
            this.questionIndex = 0;
            this.score = 0;
            this.answerTable = [];
            this.toggleScoreAreas = true;
            this.getShuffledArray(this.quiz);
        },
        
        updateUserResponces(e, i, val) {
            this.removeOnFocusClass();
            e.target.classList.add("isClicked");

            this.userResponses[i] = val;
            this.score = this.userResponses.filter(val => val).length
        },
        removeOnFocusClass(){
            const elems = document.querySelectorAll(".response--area");
            [].forEach.call(elems, function(el) {
                el.classList.remove("isClicked");
            });
        },

        //
        countDownTimerFn() {
            let _sec = (!this.countdownBlocker) ? 1000 : 0 ;

            if(!this.countdownBlocker){
                if( this.questionIndex <= (this.shuffledData.length - 1) ){
                    if(this.countdown > 0) {
                        setTimeout(() => {
                            this.countdown -= 1;
                            this.countdownTimer = moment(this.countdownTimer.subtract(1, 'seconds'));
                            this.barWidth = (100 * (this.defaultNumber - this.countdown)) / this.defaultNumber;
                            
                            this.countDownTimerFn();
                        }, _sec);
                    }
                    
                    if(this.countdown == 0) {
                        setTimeout(() => {
                            this.next();
                            this.countDownTimerFn();
                        }, _sec);
                    }
                }
            }
        },
        setCountDownData(){
            let num = this.defaultNumber; // seconds
            this.countdown = num;
            this.countdownTimer = moment(num * 1000);
            this.barWidth = 0;
        },
        ableCountDown(){
            this.countdownBlocker = !this.countdownBlocker;
            if(!this.countdownBlocker) this.countDownTimerFn();
        },

        // Set Global Functions:
        // Collect User Response & Anwsers
        getUserAnswersFn(){
            this.removeOnFocusClass();

            let _i = this.questionIndex;

            const obj = {};
            obj.title = (this.shuffledData[_i].title) ? this.shuffledData[_i].title : null;
            obj.correctAnswer = this.shuffledData[_i].responses.filter(el => el.correct === true);
            obj.isCorrect = this.userResponses[_i];
            let req = obj;

            this.answerTable.push(req);
        },
        getUserDegreeData(){
            let total = this.shuffledData.length;
            let _1st = total;
            let _2nd = total - 1;
            let _3rd = total - 2;
            let _average =(total / 2).toFixed(2);

            if(this.score === _1st){
                this.tags.icon = "001-winner.svg";
                this.tags.title = "Congratulations! you're won.";
            }
            else if(this.score === _2nd){
                this.tags.icon = "003-second.svg";
                this.tags.title = "Great! you're the 2nd.";
            }
            else if(this.score === _3rd){
                this.tags.icon = "011-third.svg";
                this.tags.title = "Great! you're the 3rd.";
            }
            else if(this.score >= _average && this.score < _3rd){
                this.tags.icon = "015-diploma.svg";
                this.tags.title = "Nice job!";
            }
            else{
                this.tags.icon = "014-error.svg";
                this.tags.title = "Oops .. you're failed!";
            }
        },

        getShuffledArray(_array){
            // Definde Array contain only [false] value based on Array length
            this.userResponses = Array(_array.questions.length).fill(false);

            // // Shuffle question responses randomly
            this.shuffledData = _array.questions.map(el => {
                el.responses.sort(() => .5 - Math.random())
                return el;
            });

            //
            this.setCountDownData();
            this.countDownTimerFn();

            this.showScore = true;
        }
    },
    components: {questionsHeader, questionsNotFound, questionsCountdownRemote, questionsScore, questionsAnswers}
}
</script>

