<template>
    <div class="main-quiz-container--section">
        <fakeQuestion v-if="!isLoaded"/>
        
        <questionsList v-if="isLoaded" :quiz="quiz"/>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import moment from 'moment';

import fakeQuestion from './../components/quizzesComponent/fakeQuestion';
import questionsList from './../components/quizzesComponent/questionsList';

export default {
    data() {
        return {
            id: this.$route.params.id,
            quiz: [],//quiz,

            //loaded data
            isLoaded: false
        }
    },
    created() {
        this.shuffleArrData();
    },
    computed: {
        timeOut: function(){
            return this.countdownTimer.format('mm:ss');
        }
    },
    methods: {
        async shuffleArrData(){
            try {
                const data = await axios({
                    method: "GET",
                    url: `/api/v1/quiz/${this.id}`
                });

                this.quiz = data.data.data.data;
                
                this.isLoaded = true;
            } catch (error) {
                return this.$router.push('/error');
            }
        }
    },
    components: {fakeQuestion, questionsList}
}
</script>

