<template>
    <div class="main-quiz-container--section main-quizzes-table--section">
        <HeaderComponent></HeaderComponent>

        <fakeQuizzes v-if="!isLoaded"/>

        <quizzesList :quizzes="quizzes" v-if="isLoaded"/>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import HeaderComponent from './../components/HeaderComponent';

import fakeQuizzes from './../components/quizzesComponent/fakeQuizzes';
import quizzesList from './../components/quizzesComponent/quizzesList';


export default {
    data() {
        return {
            quizzes: [],

            //loaded data
            isLoaded: false
        }
    },
    created() {
        this.getQuizzes();
    },
    methods: {
        async getQuizzes(){
            try {
                const data = await axios({
                    method: "GET",
                    url: "/api/v1/quiz/"
                });

                this.quizzes = data.data.data.data;
                this.isLoaded = true;
            } catch (error) {
                return this.$router.push('/error');
            }
        },
    },
    components: { HeaderComponent, fakeQuizzes, quizzesList }
}
</script>

